<template>
  <div id="customized-box">
    <div class="header-bar">
      <img class="lt-arrow" src="../../assets/arrow.png" @click="$router.go(-1)" />
      <span class="title">定制篮</span>
      <span class="edit" @click="mainEdit">{{editing ? '完成' : '编辑'}}</span>
    </div>

    <div class="customized-box">
      <div class="customized-title">
        <span class="lable">自营</span>
        <span class="name">商家名称</span>
        <span class="fl-right more">更多定制</span>
      </div>
      <div v-for="(item,index) in List" :key="index" class="customized-item">
        <div class="item-img">
          <img :src="item.image.imagePath" alt="">
        </div>
        <!-- 商品信息start -->
        <div class="item-info" v-if='!editing'>
          <div class="name">{{baseInfo.name}}</div>
          <div class="text">
            <span>已定制</span>
          </div>
          <div class="goods-price">
            <em class="price-icon">￥</em>{{baseInfo.price}}</div>

          <div class="stepper">
            <Counter :num='value' :max="5" @change='valueChange'></Counter>
          </div>

        </div>
        <!-- 商品信息end -->
        <!-- 编辑状态start -->
        <div v-for="(item,index) in List" :key="index" class="item-info2" v-else>
          <div class="name">{{baseInfo.name}}</div>
          <div class="text">
            <span>已定制</span>
          </div>
          <div class="goods-price">
            <em class="price-icon">￥</em>{{baseInfo.price}}</div>

          <div class="rt-del" @click="showDel(List)">删除</div>
        </div>
        <!-- 编辑状态end -->
      </div>
    </div>
    <!-- 底部start -->
    <!-- <div class="footer">
      <div class="goods-total">
        <div class="check-box">
          <div class="input-checkbox" :class="{ 'input-checked': isSelAll}">
            <input type="checkbox" @change="checkChange" data-type="all" data-id="0">
          </div>
        </div>
        <span class="total-text">全选</span>
        <div class="fl-right">
          <span class="total-price">合计：</span>
          <span class="price">￥{{getTotalPrice}}</span>
          <span class="goods-account" @click="linkToConfirmOrder" v-show="!editing">立即结算</span>
          <span class="goods-account" @click="delAll" v-show="editing">立即删除</span>
        </div>
      </div>
    </div> -->
    <div class="footer">
      <div class="goods-total">
        <!-- <div class="check-box">
          <div class="input-checkbox" :class="{ 'input-checked': isSelAll}">
            <input type="checkbox" data-type="all" data-id="0">
          </div>
        </div> -->
        <div class="total-text">
          <div class="menu">
            <div class="menu__logo">
              <img src="../../assets/server.png" alt="">
            </div>
            <div class="menu__name">客服</div>
          </div>
        </div>
        <div class="fl-right">
          <span class="total-price">合计：</span>
          <span class="price">￥{{price}}</span>
          <span class="goods-account" v-show="!editing" @click="getCartUpdate()">提交定制</span>
          <span class="goods-account" v-show="editing">立即删除</span>
        </div>
      </div>
    </div>
    <!-- 底部end -->
  </div>
</template>
<script>
//   import Cell from '@/components/Cell/Cell';
import Counter from "@/components/Counter/Counter";

export default {
  data() {
    return {
      value: 1,
      editing: false,
      good: 1,
      isSelAll: false,

      List: [],
      baseInfo: {},
      price: 0
    };
  },
  watch: {},
  created() {
    this.customizedBoxList(this.$route.query.id, 2);
  },
  methods: {
    //定制列表
    customizedBoxList(id, a) {
      this.$Api.getDesignInfo(id, a).then(res => {
        // console.log(res.q.attrs)
        this.List = res.q.attrs;
        localStorage.setItem('List',JSON.stringify(this.List))
        this.baseInfo = JSON.parse(localStorage.getItem("item"));
      });
    },
    submit() {
      this.$router.push({ name: "CustomizedConfirmStep1", query: {id:this.$route.query.id,num:this.value}});
    },
    getCartUpdate(){      
    let a = 1
    let goods = JSON.parse(localStorage.getItem('goods'))
    let goodsList =  [{
      skuId: goods.skuId,
      spuId: goods.spuId,
      num: this.value,
      customId: parseInt(this.$route.query.id),
      promId: 0,
      packingId: 0
    }]
      this.$Api.getCartUpdate(a,goodsList).then(res=>{
        console.log(res.q.d)
        this.$router.push({ name: "CustomizedConfirmStep1", query: {id:this.$route.query.id,num:this.value}});
      })
    },
    //计算值
    valueChange(val) {
      this.value += val;
      this.price = this.value * (this.baseInfo.price || 0);
    },
    //编辑Start
    //编辑全部
    mainEdit(e) {
      this.editing = !this.editing;
      if (this.editing) this.editItem = {};
    },
    /*购物车选择 end*/
    showDel(good) {
      console.log(good);
      let goodInfo = good;
      console.log(goodInfo);
      this.$popup({
        content: "确定要删除选中的商品吗？"
      }).then(res => {
        if (res == "sure") {
          this.editing = false;
          goodInfo.splice(0, 1);
          this.price = 0;
          // console.log(goodInfo);
          // let params = {
          //   skuId: goodInfo.skuId,
          //   spuId: goodInfo.spuId,
          //   num: 0
          // };
          // this.getCartUpdate(2, params);
        }
      });
    }
  },
  components: {
    Counter
  }
};
</script>


<style lang='scss'>
@import "../../sass/baseSass.scss";
#customized-box {
  /*头部样式*/
  .header-bar {
    /*display: flex;*/
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
    height: 1.17rem;
    line-height: 1.17rem;
    /*align-items: center;*/
    padding: 0 0.4rem;
    text-align: center;
    color: #fff;
    @include bg-color;
    .lt-arrow {
      /*@include lt-arrow;*/
      display: inline-block;
      @include lt-arrow;
    }
    .title {
      font-size: 0.48rem;
    }
    .edit {
      position: absolute;
      right: 0.4rem;
      font-size: 0.4rem;
    }
  }
  .customized-box {
    padding-top: 1.17rem;
    .customized-title {
      position: relative;
      height: box(80);
      line-height: box(80);
      padding-left: box(35);
      padding-right: box(65);
      font-size: box(28);
      border-bottom: 1px solid $border-color;
      .lable {
        color: #de7fef;
        padding-right: box(18);
      }
      &:after {
        content: "";
        position: absolute;
        top: 50%;
        right: 0.4rem;
        width: 0.173rem;
        height: 0.307rem;
        transform: translateY(-50%);
        background: url("../../assets/arrow_gray.png");
        background-size: 100%;
      }
    }
    .customized-item {
      position: relative;
      display: flex;
      height: box(200);
      border: 1px solid $border-color;
      padding: box(20) box(30);
      img {
        width: box(160);
        height: box(160);
        border-radius: 8px;
      }
    }
    .item-info {
      position: relative;
      flex: 1;
      padding: box(8) box(30) box(20) box(28);

      .name {
        font-size: box(26);
        font-weight: 600;
        @include textflow-two;
        color: $text-color;
        margin-bottom: box(15);
      }
      .text {
        font-size: box(23);
        color: $text-color2;
      }
      .goods-price {
        padding-top: box(29);
        font-size: box(24);
        font-weight: bold;
        color: $price-color;
        .price-icon {
          font-size: box(17);
        }
      }
      .stepper {
        position: absolute;
        bottom: 0;
        right: 0;
      }
    }
    .item-info2 {
      /* position: relative; */
      flex: 1;
      padding: box(8) box(30) box(20) box(28);
      .name {
        font-size: box(26);
        font-weight: 600;
        @include textflow-two;
        color: $text-color;
        margin-bottom: box(15);
      }
      .text {
        font-size: box(23);
        color: $text-color2;
      }
      .goods-price {
        padding-top: box(29);
        font-size: box(24);
        font-weight: bold;
        color: $price-color;
        .price-icon {
          font-size: box(17);
        }
      }
      .rt-del {
        position: absolute;
        right: 0;
        top: 0;
        font-size: 0.4rem;
        width: 1.6rem;
        height: 2.67rem;
        line-height: 2.67rem;
        background: #fe4436;
        color: #fff;
        text-align: center;
        border-bottom: 1px solid $border-color;
      }
    }
  }
  .footer {
    border-top: 1px solid $border-color;
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    background: #fff;
    height: 1.31rem;
    line-height: 1.31rem;
    .goods-total {
      font-size: 0;
      height: 1.31rem;
      line-height: 1.31rem;
      width: 100%;
      padding-left: box(60);
      .check-box {
        display: inline-block;
        width: 0.8rem;
        height: 1.31rem;
        position: relative;
        line-height: 1.31rem;
        .input-checkbox {
          position: absolute;
          top: 50%;
          /*left: 50%;*/
          margin-top: -0.28rem;
          /*margin-left: -0.28rem;*/
        }
      }
      .total-text {
        display: inline-block;
        height: 1.31rem;
        line-height: 1.31rem;
        font-size: 0.37rem;
        color: $text-color;
        vertical-align: top;
        .menu {
          position: relative;
          /* display: inline-block;
            text-align: center;
            width: 25%;
            height: 2.4rem;
            font-size: 0.347rem;
            color: #333; */
          .menu__logo {
            width: box(50);
            //     height:box(50);
            /* margin-top: 0.72rem;
              margin-bottom: 0.225rem;
              position: relative;
              display: inline-block;
              height: 0.747rem;
              vertical-align: middle; */
          }

          img {
            height: 0.747rem;
            position: absolute;
            top: box(12);
            left: 0;
            vertical-align: middle;
          }
          .menu__name {
            padding-top: box(33);
            font-size: box(21);
            color: $text-color2;
          }
        }
      }
      .total-price {
        font-size: 0.4rem;
        color: $text-color;
        margin-right: 0.27rem;
      }
      .price {
        color: $price-color;
        font-size: 0.4rem;
        margin-right: 0.4rem;
      }
      .goods-account {
        display: inline-block;
        font-size: 0.43rem;
        color: #fff;
        @include bg-color;
        width: 2.91rem;
        height: 1.31rem;
        line-height: 1.31rem;
        text-align: center;
      }
    }
  }
}
</style>



